<template>
  <div class="container">
    <div class="shop" :class="{'shop-bg':isSelfSale || hasPublicDomain}">
      <div class="left">
        <img class="logo" :src="logoUrl" />
      </div>
      <div class="center">
        <div class="shop-name van-ellipsis">{{ storeInfo?(hasPublicDomain?storeInfo.storePublicDomainName:storeInfo.o2OStoreName):selfSale }}
          <span v-if='hasPublicDomain' class='tag'>官方</span>
          <span v-if='isSelfSale' class='tag'>自营</span>
        </div>
        <div class="slogan">品质溯源 | 同城派送</div>
        <div class="btn-wrap" v-if='storeInfo && !hasPublicDomain'>
          <!-- <a class="call btn" :href="`tel:${storeInfo.mobile}`">拨打电话</a> -->
          <div @click="fnOpenShopDetail" class="call btn">进店逛逛</div>
          <div class="call btn right" @click="fnOpenShopDetail">全部商品</div>
          <!-- <div class="call btn" @click="fnOpenShop" v-if="storeInfo.nearbyId">线下体验</div> -->
        </div>
      </div>
      <!-- <div class="right">
