    </div>
  </div>
</template>

<script>
export default {
  props: ['storeInfo', 'stockInfo'],
  data () {
    return {}
  },
  computed: {
    logoUrl() {
      if (this.storeInfo && this.storeInfo.avatarImageFileId) {
        return this.storeInfo.avatarImageFileId
      } else if (this.$util.isSyBird() || (this.storeInfo && Number(this.storeInfo.synStoreType) !== 9)) {
        return `${this.$cdnImageUrl}/sybird_default_logo.png`
      } else {
        return `${this.$cdnImageUrl}/goodsDetail/touxiang.png`
      }
    },
    isSelfSale() {
      return this.stockInfo.stockType === 'WA' || this.stockInfo.stockType === 'ZJ'
    },
    hasPublicDomain() {
      return this.storeInfo && this.storeInfo.storePublicDomainName;
    },
    selfSale () {
      return ('海尔智家旗舰店')
    }
  },
  methods: {
    fnOpenShopDetail () {
      this.$router.push({
        path: '/shopDetail',
        query: {
          storeId: this.storeInfo.o2oStoreId,
        },
      })
    },
    // fnOpenShop () {
    //   this.$router.push({
    //     path: '/nearshop',
    //     query: {
    //       nearStoreId: this.storeInfo.nearbyId,
    //     },
    //   })
    // },
  },
}
</script>

